<template>
  <v-card class="mx-auto">
    <v-card-title>
      <v-row justify="space-between">
        <v-col cols="12" sm="4">
          Заявки
        </v-col>
        <v-col cols="12" sm="4">
          <v-select
            label="Категория" 
            :items="groups"
            v-model="group_type"
          ></v-select>
        </v-col>
        <v-col cols="12" sm="4">
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Поиск"
          ></v-text-field>
        </v-col>
      </v-row>
    </v-card-title>
    <v-dialog 
      v-model="dialog" 
      max-width="540px"
    >
      <v-card>
        <v-card-title>
          {{ dialogTitle }}
          <v-spacer></v-spacer>
          <v-btn 
            color="error"
            icon
            :loading="loading"
            @click="checkWithdrawal()"
          >
            <v-icon>mdi-reload-alert</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn 
            color="blue darken-1" 
            text
            @click="cancelWithdrawal()"
          >
            Отмена
          </v-btn>
          <v-btn
            color="blue darken-1" 
            text
            @click="acceptWithdrawal()"
          >
            Подтверждение
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-data-table
      :headers="headers"
      :items="filteredItems"
      :search="search"
    >
      <template v-slot:item.userId="props">
        <v-edit-dialog
          :return-value.sync="props.item.userId"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.userId }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.userId"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.date="props">
        <v-edit-dialog
          :return-value.sync="props.item.date"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.date }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.date"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.player="props">
        <v-edit-dialog
          :return-value.sync="props.item.player"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <v-avatar 
            size="35" 
            class="mr-2"
          >
            <img :src="props.item.src">
          </v-avatar>
          <span>{{ props.item.player }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.player"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
            <v-text-field
              v-model="props.item.src"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.operator="props">
        <v-edit-dialog
          :return-value.sync="props.item.operator"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.operator }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.operator"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.paymentSystem="props">
        <v-edit-dialog
          :return-value.sync="props.item.paymentSystem"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.paymentSystem }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.paymentSystem"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.wallet="props">
        <v-edit-dialog
          :return-value.sync="props.item.wallet"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.wallet }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.wallet"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.amount="props">
        <v-edit-dialog
          :return-value.sync="props.item.amount"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.amount }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.amount"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.withdrawal="props">
        <v-edit-dialog
          :return-value.sync="props.item.withdrawal"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.withdrawal }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.withdrawal"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.status="props">
        <v-edit-dialog
          :return-value.sync="props.item.status"
          large
          persistent
          @save="save"
          @cancel="cancel"
          @open="open"
          @close="close"
        >
          <span>{{ props.item.status }}</span>
          <template v-slot:input>
            <v-text-field
              v-model="props.item.status"
              label="Edit"
              single-line
              counter
              autofocus
            ></v-text-field>
          </template>
        </v-edit-dialog>
      </template>
      <template v-slot:item.action="{ item }">
        <v-btn 
          @click="acceptDialog(item)"
          icon
        >
          <v-icon small>
            mdi-check-circle-outline
          </v-icon>
        </v-btn>
        <v-btn 
          @click="cancelDialog(item)"
          icon
        >
          <v-icon small>
            mdi-close-circle-outline
          </v-icon>
        </v-btn>
      </template>
    </v-data-table>

    <v-snackbar 
      v-model="snack" 
      :timeout="3000" 
      :color="snackColor"
    >
      {{ snackText }}
      <v-btn 
        text 
        @click="snack = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogTitle: '',
    loading: false,
    snack: false,
    snackColor: '',
    snackText: '',
    groups: ['All', 'Done', 'Process', 'Cancel'],
    group_type: null,
    search: '',
    headers: [
      { text: 'ID', align: 'left', value: 'userId' },
      { text: 'Дата', value: 'date' },
      { text: 'Игрок', value: 'player' },
      { text: 'Оператор', value: 'operator' },
      { text: 'Платежная система', value: 'paymentSystem' },
      { text: 'Кошелек', value: 'wallet' },
      { text: 'Сумма заявки', value: 'amount' },
      { text: 'Сумма вывода', value: 'withdrawal' },
      { text: 'Статус', value: 'status' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    data: [
      { 
        userId: 1,
        date: new Date().toISOString().substr(0, 10),
        player: 'Андрей',
        operator: 'MBANK',
        paymentSystem: 'y.ru',
        wallet: 'qiwi',
        amount: 150,
        withdrawal: 100,
        status: 'done',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 2,
        date: new Date().toISOString().substr(0, 10),
        player: 'Саша',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 250,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 3,
        date: new Date().toISOString().substr(0, 10),
        player: 'Ваня',
        operator: 'MBANK',
        paymentSystem: 'z.ru',
        wallet: 'qiwi',
        amount: 1000,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 4,
        date: new Date().toISOString().substr(0, 10),
        player: 'Катя',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 200,
        withdrawal: 500,
        status: 'cancel',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 5,
        date: new Date().toISOString().substr(0, 10),
        player: 'Артем',
        operator: 'MBANK',
        paymentSystem: 'y.ru',
        wallet: 'qiwi',
        amount: 600,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 6,
        date: new Date().toISOString().substr(0, 10),
        player: 'Даша',
        operator: 'MBANK',
        paymentSystem: 'z.ru',
        wallet: 'qiwi',
        amount: 400,
        withdrawal: 100,
        status: 'cancel',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 7,
        date: new Date().toISOString().substr(0, 10),
        player: 'Иван',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 1010,
        withdrawal: 1000,
        status: 'done',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      }
    ]
  }),
  computed: {
    filteredItems() {
      return this.data.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  },
  methods: {
    save() {
      this.snack = true;
      this.snackColor = 'success';
      this.snackText = 'Данные обновлены';
    },
    cancel() {
      this.snack = true;
      this.snackColor = 'error';
      this.snackText = 'Отмена';
    },
    open() {
      this.snack = true;
      this.snackColor = 'info';
      this.snackText = 'Редактирование';
    },
    close() {
      console.log('Dialog closed');
    },
    cancelDialog(user) {
      this.dialogTitle = 'Отклонение заявки на вывод средств';
      console.log('Open dialog');
      this.dialog = true;
    },
    acceptDialog(user) {
      this.dialogTitle = 'Подтверждение заявки на вывод средств';
      console.log('Open dialog');
      this.dialog = true;
    },
    checkWithdrawal() {
      this.loading = true;
      setTimeout(() => {
        this.dialog = false;
        this.loading = false;
      }, 1500)
    },
    cancelWithdrawal() {
      this.dialog = false;
    },
    acceptWithdrawal() {
      this.dialog = false;
    }
  }
}
</script>
